.buttonsContainer{
    flex-shrink: 0;
    margin-left: 7px;
}

.buttonsContainer .btn{
    border-radius: 0px;
}

.buttonsContainer .btn+.btn{
    border-left: 0px;
}

.buttonsContainer .first{
    border-radius: 4px 0 0 4px;
}

.buttonsContainer .last{
    border-radius: 0 4px 4px 0;
}
